.side-menu {
  float: left;
  position: fixed;
  width: auto;
  height: auto;
  background: #efefef;
  border: 1px solid grey;
  overflow: hidden;
  z-index: 50;
  padding: 2px;
}
.side-menu.right {
  float: right;
  right: 0;
}
.side-menu .hide {
  display: none;
}
.side-menu a {
  display: block;
  float: none;
  overflow: hidden;
  line-height: 1em;
}
.side-menu img,
