body {
  text-align: left;
}

.table th > span:empty::before,
.table td > span:empty::before {
  content: "";
}

@media only screen and (width >= 768px) {
  .table th.position-sticky {
    top: 96px;
  }
}
select#fieldsSelect,
textarea#sqlquery {
  height: 20rem;
}

.breadcrumb-navbar {
  padding: 0.5rem 1.8rem;
  margin-bottom: 0;
  background-color: #e9ecef;
}
.breadcrumb-navbar .breadcrumb-item + .breadcrumb-comment {
  padding-left: 0.5rem;
}
.breadcrumb-navbar .breadcrumb-item .icon {
  margin: 0;
}

[data-bs-theme=dark] .breadcrumb-navbar {
  background-color: #343a40;
}

@media print {
  .hide {
    display: none;
  }
  body,
  table,
  th,
  td {
    color: #000;
    background-color: #fff;
  }
  a:link {
    color: #000;
    text-decoration: none;
  }
  img {
    border: 0;
  }
  table,
  th,
  td {
    border: 0.1em solid #000;
    background-color: #fff;
  }
  table {
    border-collapse: collapse;
    border-spacing: 0.2em;
  }
  thead {
    border-collapse: collapse;
    border-spacing: 0.2em;
    border: 0.1em solid #000;
    font-weight: 900;
  }
  th,
  td {
    padding: 0.2em;
  }
  thead th {
    font-weight: bold;
    background-color: #e5e5e5;
    border: 0.1em solid #000;
  }
  #page_content {
    position: absolute;
    left: 0;
    top: 0;
    width: 95%;
    float: none;
  }
  .cDrop,
  .cEdit,
  .cList,
  .cCpy,
  .cPointer {
    display: none;
  }
  table tbody:first-of-type tr:nth-child(odd) {
    background: #fff;
  }
  table tbody:first-of-type tr:nth-child(odd) th {
    background: #fff;
  }
  table tbody:first-of-type tr:nth-child(even) {
    background: #dfdfdf;
  }
  table tbody:first-of-type tr:nth-child(even) th {
    background: #dfdfdf;
  }
  .column_attribute {
    font-size: 100%;
  }
}
.card-img-container {
  height: 16vh;
  overflow: hidden;
}
.card-img-container img {
  object-fit: cover;
}